import browser from 'webextension-polyfill';
import { serializeError } from 'serialize-error';
import BackgroundError from 'telemetry/Error';

import config from 'config';

import { MESSAGE_CONTEXT_ERROR } from './contexts';
import { MESSAGE_TYPE_RECORD_ERROR } from './types';

/**
 * Checks if a chrome message sent to a tab content script is valid
 * @param  {Object} The message sender object that includes tab info
 * @returns  {Boolean}
 */
const isValidMessenger = (sender) => sender?.id === config.extensionId;

/**
 * Function to send errors to the background script for logging
 * @param {Object} error
 * @param {Object} customProps
 */
const sendErrorToBackground = async (error, errorDetails = {}) =>
	browser.runtime.sendMessage(config.extensionId, {
		context: MESSAGE_CONTEXT_ERROR,
		type: MESSAGE_TYPE_RECORD_ERROR,
		error: serializeError(error),
		data: errorDetails,
	});

// TODO: This technically sends the message to the background script and content scripts
// so there's a chance a message could be handled twice if a content script sends a message to
// the background and then the background sends the same message back to the tab thinking that the
// tab couldn't receive the original message. Maybe change this function name to sendMessageExtension or
// something like that. Note: it only sends the message to all extension scripts on the current tab.
/**
 * Message wrapper for browser.runtime.sendMessage
 * @param message
 * @returns {Promise<any>}
 */
const sendMessageToBackground = async (message) => {
	try {
		return await browser.runtime.sendMessage(config.extensionId, message);
	} catch (error) {
		const handledError = new BackgroundError(
			error.message,
			'sendMessageToBackground'
		);

		if (!handledError.ignore) {
			throw handledError;
		}
	}
};

const sendMessageToTab = async (tabId, message) => {
	try {
		return await browser.tabs.sendMessage(tabId, message);
	} catch (error) {
		const handledError = new BackgroundError(
			error.message,
			'sendMessageToTab'
		);

		if (!handledError.ignore) {
			throw handledError;
		}
	}
};

const sendMessageToAllTabs = async (message) => {
	const tabs = await browser.tabs.query({});
	for (const tab of tabs) {
		await browser.tabs.sendMessage(tab.id, message);
	}
};

export {
	isValidMessenger,
	sendErrorToBackground,
	sendMessageToBackground,
	sendMessageToTab,
	sendMessageToAllTabs,
};
