// TODO: This was copied from `@seamless/core` and should be replaced with the version there
// when that package has better code splitting support, b/c currently the build throws
// errors when an imported method is used in a web worker.
//
// If this is to be imported anywhere other than a web worker, use the module from '@seamless/core'

/**
 * Check if value is the language type of Object
 * @param {any} val - The value to test
 * @return {boolean} - True if the value is an Object, otherwise false.
 * example
 * isObject({}); // -> true
 * isObject([]); // -> true
 */
const isObject = (val) => {
	const type = typeof val;
	return !!val && (type === 'function' || type === 'object');
};

export default isObject;
