// TODO: This was copied from `@seamless/core` and should be replaced with the version there
// when that package has better code splitting support, b/c currently the build throws
// errors when an imported method is used in a web worker.
//
// If this is to be imported anywhere other than a web worker, use the module from '@seamless/core'

/**
 * Check if value is a function
 * @param {any} val - The value to test
 * @return {boolean} - True if the value is a Function, otherwise false.
 * example
 * isFunction(function() {}); // -> true
 * isFunction(function*() {}); // -> true
 * isFunction(async function() {}); // -> true
 */
const isFunction = (val) => {
	const objectString = Object.prototype.toString.call(val);

	return (
		objectString === '[object Function]' ||
		objectString === '[object GeneratorFunction]' ||
		objectString === '[object AsyncFunction]'
	);
};

export default isFunction;
