import config from 'config';

class BackgroundError extends Error {
	// Not passing in a type will default to handling the error
	constructor(message, type) {
		super(message);

		const errorsOfType = config.handledErrors[type] || [];

		const errorConfig = errorsOfType.find((err) =>
			new RegExp(err.match).test(message)
		);

		this.ignore = !type ? false : !!errorConfig?.ignore;
		this.skipRollbar = !type ? true : !!errorConfig;
		this.name = errorConfig || !type ? 'HandledError' : 'UnhandledError';
	}
}

export default BackgroundError;
