// TODO: This was copied from `@seamless/core` and should be replaced with the version there
// when that package has better code splitting support, b/c currently the build throws
// errors when an imported method is used in a web worker.
//
// If this is to be imported anywhere other than a web worker, use the module from '@seamless/core'

import isObject from './isObject';

/**
 * Check if value is an empty object or array.
 * @param {any} val - The value to check
 * @return {boolean} - True if the value is empty
 * example
 * isEmpty([]); // -> true
 * isEmpty({}); // -> true
 */
const isEmpty = (val) => {
	if (val == null) {
		return true;
	}

	if (Array.isArray(val)) {
		return val.length === 0;
	}

	return isObject(val) && Object.keys(val).length === 0;
};

export default isEmpty;
