import browser from 'webextension-polyfill';
import isEmpty from 'utils/coreDupes/isEmpty';
import isPlainObject from 'utils/coreDupes/isPlainObject';
import { isValidMessenger, sendMessageToBackground } from 'common/messaging';
import config from 'config';
import {
	MESSAGE_CONTEXT_DATA_EXTRACTOR,
	MESSAGE_CONTEXT_IS_INJECTED,
} from 'common/messaging/contexts';
import { MESSAGE_PORT_LIFECYCLE_PAGE_DATA_EXTRACTOR } from 'common/messaging/portLifecycle';
import {
	MESSAGE_TYPE_GET_CRM_ACCOUNT_DETAILS,
	MESSAGE_TYPE_INJECT_PAGE_DATA_EXTRACTOR,
	MESSAGE_TYPE_PROCESS_EXTRACTED_DATA,
} from 'common/messaging/types';
import { PAGE_DATA_EXTRACTOR_ELEMENT_SELECTOR } from 'contentScripts/linkedIn/constants';

let TAB_ID;

const rawPort = browser.runtime.connect(config.extensionId, {
	name: MESSAGE_PORT_LIFECYCLE_PAGE_DATA_EXTRACTOR,
});

const extractPageData = () => {
	const dataEls = document.querySelectorAll(
		PAGE_DATA_EXTRACTOR_ELEMENT_SELECTOR
	);
	if (dataEls?.length) {
		const scriptData = Array.from(dataEls);
		scriptData.forEach((data) => {
			const pageData = data?.innerText
				? JSON.parse(data.innerText)
				: data;

			if (
				!isEmpty(pageData) &&
				isPlainObject(pageData) &&
				!pageData.request
			) {
				sendMessageToBackground({
					context: MESSAGE_CONTEXT_DATA_EXTRACTOR,
					type: MESSAGE_TYPE_PROCESS_EXTRACTED_DATA,
					data: {
						pageData,
						source: 'html',
					},
				});
			}
		});
	}
};

// TODO: move this to a per page config or something
const getCrmAccountDetails = (pageModule) => {
	const { domainLookupConfig } = pageModule || {};
	if (!domainLookupConfig) {
		return {};
	}

	const titleContent = document.title;
	const titleContentRegexStr = domainLookupConfig.titleAccountRegex;
	const crmAccountName = titleContentRegexStr
		? titleContent?.match(new RegExp(titleContentRegexStr))?.[0]
		: titleContent;

	const websiteEl = document.querySelector(
		domainLookupConfig.websiteSelector
	);

	const crmAccountWebsite = websiteEl?.textContent;

	return { crmAccountName, crmAccountWebsite };
};

if (['complete', 'interactive'].indexOf(window.document.readyState) > -1) {
	extractPageData();
} else {
	// TODO: need to test 'load' vs 'DOMContentLoaded'
	// 'load' event is triggered only after the page is fully-loaded.
	// possibly better to to use 'DOMContentLoaded' which is DOM loaded but not all resources
	// window.addEventListener('load', extractPageData, { once: true });
	window.addEventListener('DOMContentLoaded', extractPageData, {
		once: true,
	});
}

const handleOnPortMessage = (message) => {
	if (
		message.context === MESSAGE_PORT_LIFECYCLE_PAGE_DATA_EXTRACTOR &&
		message.data?.tabId &&
		!TAB_ID
	) {
		({ tabId: TAB_ID } = message.data);
	}
};

const handleOnBrowserMessage = (message, sender) => {
	const { context, type, data } = message;
	if (isValidMessenger(sender)) {
		if (
			context === MESSAGE_CONTEXT_IS_INJECTED &&
			type === MESSAGE_TYPE_INJECT_PAGE_DATA_EXTRACTOR
		) {
			return Promise.resolve(true);
		}

		if (
			context === MESSAGE_CONTEXT_DATA_EXTRACTOR &&
			type === MESSAGE_TYPE_GET_CRM_ACCOUNT_DETAILS
		) {
			return Promise.resolve(getCrmAccountDetails(data?.pageModule));
		}
	}
};

browser.runtime.onMessage.addListener(handleOnBrowserMessage);

rawPort.onMessage.addListener(handleOnPortMessage);
rawPort.onDisconnect.addListener(() => {
	browser.runtime.onMessage.removeListener(handleOnBrowserMessage);
	rawPort.onMessage.removeListener(handleOnPortMessage);
});
