// TODO: This was copied from `@seamless/core` and should be replaced with the version there
// when that package has better code splitting support, b/c currently the build throws
// errors when an imported method is used in a web worker.
//
// If this is to be imported anywhere other than a web worker, use the module from '@seamless/core'

import isFunction from './isFunction';
import isObject from './isObject';

/**
 * Check if value is an object created by Object constructor.
 * @param {any} val - The value to test
 * @return {boolean} - True if the value is a plain Object, otherwise false.
 * example
 * isPlainObj({}); // -> true
 * isPlainObj([]); // -> false
 * isPlainObj(function() {}); // -> false
 */
const isPlainObject = (val) => {
	if (!isObject(val)) {
		return false;
	}

	const ctor = val.constructor;
	if (!isFunction(ctor)) {
		return false;
	}

	if (
		!Object.prototype.hasOwnProperty.call(ctor.prototype, 'isPrototypeOf')
	) {
		return false;
	}

	return !Array.isArray(val) && !isFunction(val);
};

export default isPlainObject;
